import * as React from 'react';
import { useNavigate } from 'react-router-dom';

import './buttons.scss';

import { getClassName as getCN, isFunction as isF, getWindowQuery } from '@miq/utiljs';
import { Portal } from '../Modal';

//#region Button

type ButtonProps = {
  title?: string;
  disabled?: boolean;
  onClick?: React.DOMAttributes<HTMLButtonElement> | Function;
  children: React.ReactNode;
  className?: string;
  id?: string;
};

export const Button: React.FC<ButtonProps> = React.forwardRef(({ children, ...props }, ref) => {
  return (
    <button type="button" {...props} className={getCN(['btn', props.className])} ref={ref}>
      {children}
    </button>
  );
});

Button.displayName = 'Button';

//#endregion Button

//#region QueryButton

type QueryButtonProps = ButtonProps & {
  query?: any;
  navOnClick?: boolean;
};

export const QueryButton = (props: QueryButtonProps) => {
  const navigate = useNavigate();
  let { query, onClick, navOnClick = true, ...rest } = props;
  if (!query) query = getWindowQuery();

  const handleClick = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    onClick?.({ e, query });
    if (!navOnClick) return;
    const path = new URL(window.location.href);
    navigate(`${path.pathname}?${query}`);
  };
  return <Button {...rest} onClick={handleClick} />;
};

//#endregion QueryButton

//#region ConfirmButton

type ConfirmButtonProps = ButtonProps & {
  readonly modal: { width: string };
  render: Function;
  renderHeader: Function;
  renderFooter: Function;
};

export const ConfirmButton = ({
  children,
  render,
  renderHeader,
  renderFooter,
  ...props
}: ConfirmButtonProps): JSX.Element => {
  const [isOpen, setOpen] = React.useState<boolean>(false);
  const { modal = {}, ...rest } = props;
  return (
    <>
      <Button {...rest} onClick={() => setOpen(!isOpen)}>
        {children}
      </Button>
      {isOpen && (
        <Portal>
          <Portal.Modal
            {...modal}
            width={modal.width}
            header={isF(renderHeader) && renderHeader({ isOpen, setOpen })}
            footer={isF(renderFooter) && renderFooter({ isOpen, setOpen })}
          >
            {isF(render) && render({ isOpen, setOpen })}
          </Portal.Modal>
        </Portal>
      )}
    </>
  );
};

//#endregion ConfirmButton
