import { isString } from './helpers';
import isDate from 'lodash.isdate';
import { logError } from '.';

//#region DateMixin

/**
 * Date Object Mixin
 */

export const DateMixin = {
  getValue() {
    return new Date(this.valueOf());
  },
  isSameDayAs(date: Date) {
    if (!isDate(date)) {
      logError('Cannot compare Date and', typeof date);
      return false;
    }
    return formatDateToStr(date) === formatDateToStr(this.getValue());
  },
  isToday() {
    return formatDateToStr(new Date()) === formatDateToStr(this.getValue());
  },
  /**
   * @returns {Date}
   */
  yesterday() {
    return this.subDays(1);
  },
  /**
   * @param {Number} count
   */
  subDays(count: number) {
    const currentDate = this.getValue();
    currentDate.setDate(currentDate.getDate() - count);
    return currentDate;
  },
  /**
   * @returns {Date}
   */
  tomorrow() {
    return this.addDays(1);
  },
  /**
   * @param {Number} count
   */
  addDays(count: number) {
    const currentDate = new Date(this.valueOf());
    currentDate.setDate(currentDate.getDate() + count);
    return currentDate;
  },
  /** Return an array of dates of a given date's week
   * @returns {Date[]}
   */
  getWeekDates() {
    const currentDate: Date = new Date(this.valueOf());
    return currentDate.getDateArray(currentDate.getWeekFirstDt(), currentDate.getWeekLastDt());
  },
  /**
   * @returns {Date}
   */
  getWeekFirstDt() {
    const currentDate = new Date(this.valueOf());
    const firstDt = new Date(currentDate);
    const offset = currentDate.getDate() - currentDate.getDay();
    firstDt.setDate(offset);
    return firstDt;
  },
  /**
   * @returns {Date}
   */
  getWeekLastDt() {
    const currentDate = new Date(this.valueOf());
    const firstDt = currentDate.getWeekFirstDt();
    const offset = firstDt.getDate() + 6;
    firstDt.setDate(offset);
    return firstDt;
  },
  getMonthFirstDt() {
    const cD = new Date(this.valueOf());
    return new Date(cD.getFullYear(), cD.getMonth(), 1);
  },
  getMonthLastDt() {
    const cD = new Date(this.valueOf());
    return new Date(cD.getFullYear(), cD.getMonth() + 1, 0);
  },
  /**
   * Return an array of dates between two given dates
   * @returns {Date[]}
   */
  getDateArray(startDate: Date, endDate: Date): Date[] {
    let currentDate = startDate;
    const dates = [];
    while (currentDate <= endDate) {
      dates.push(currentDate);
      currentDate = currentDate.tomorrow();
    }
    return dates;
  },
};

Object.assign(Date.prototype, DateMixin);

//#endregion DateMixin

/**
 * Formats a date. Returns empty string if date undefined or null.
 * Format options: {weekday: "short",month: "long",day: "numeric",hour: "numeric",minute: "numeric",year: 'numeric'}
 *
 * @param {*} date
 * @param {*} format
 * @param {*} locale
 * @returns
 */

export const formatDate = (
  date: Date | string,
  format = { weekday: 'short', month: 'long', day: 'numeric' },
  locale = new Intl.NumberFormat().resolvedOptions().locale || 'en-US'
) => {
  if (!date || (!isString(date) && !isDate(date))) return date;

  const date_time = new Intl.DateTimeFormat(locale, { ...format });
  return date_time.format(new Date(date));
};

export const formatDateToStr = (date: Date) => {
  if (!isDate(date)) return '';

  var dd = date.getDate();
  var mm = date.getMonth() + 1; //January is 0!
  var yyyy = date.getFullYear();

  if (dd < 10) {
    dd = '0' + dd;
  }
  if (mm < 10) {
    mm = '0' + mm;
  }

  return yyyy + '-' + mm + '-' + dd;
};

export const formatTime = (
  date: Date,
  format = { hour: 'numeric', minute: 'numeric' },
  locale = new Intl.NumberFormat().resolvedOptions().locale
) => {
  const date_time = new Intl.DateTimeFormat(locale, { ...format });
  return date_time.format(new Date(date));
};
