import * as React from 'react';

import { AbstractCrudStaffModel, AbstractModel, getClassName as getCN, getImgUrl } from '@miq/utiljs';

import './img.scss';

//#region COMPONENTS

export type ImgProps = {
  slug: string;
  src: string;
  height?: number;
  width?: number;
  size?: string;

  alt_text?: string;
  caption?: string;
  position?: number;

  thumb?: string;
  height_thumb?: number;
  width_thumb?: number;
  size_thumb?: string;
  thumb_sq?: string;
  height_thumb_sq?: number;
  width_thumb_sq?: number;
  size_thumb_sq?: string;
  src_mobile?: string;
  height_mobile?: number;
  width_mobile?: number;
  size_mobile?: string;

  id?: string;
  className?: string;
  name?: string;
  name_truncated?: string;
  created?: string;
  updated?: string;
  title?: string;
  aria_label?: string;
};

type GalleryProps = { images: any[]; mobileOnly?: string; className?: string; id?: string };

type HorizontalGalleryProps = GalleryProps & {};

const HorizontalGallery = ({ images = [], mobileOnly, ...props }: HorizontalGalleryProps) => {
  if (!images) return null;

  return (
    <div id={props.id} className={getCN(['imgs-gallery-horizontal', props.className])}>
      <div className={getCN(['imgs-gallery-horizontal-inner', mobileOnly && 'md-vertical'])}>
        {images.map((img) => {
          return (
            <div className="gallery-item" key={img.slug || img.src}>
              <Picture {...img} />
            </div>
          );
        })}
      </div>
    </div>
  );
};

type VerticalGalleryProps = GalleryProps & {};

const VerticalGallery = ({ images = [], ...props }: VerticalGalleryProps) => {
  return (
    <div id={props.id} className={getCN(['imgs-gallery-vertical', props.className])}>
      {images.map((img) => {
        return (
          <div className="gallery-item mb-1" key={img.slug || img.src}>
            <Picture {...img} />
          </div>
        );
      })}
    </div>
  );
};

const Mobile = (props: ImgProps) => {
  const src = getImgUrl(props.src);
  const thumb = getImgUrl(props.thumb);
  const thumb_sq = getImgUrl(props.thumb_sq);
  const src_mobile = getImgUrl(props.src_mobile);

  return (
    <div className="miq-img-mobile-wrapper">
      <Image
        {...props}
        src={src_mobile || src}
        thumb={src_mobile || thumb}
        thumb_sq={src_mobile || thumb_sq}
        className={getCN(['miq-img-mobile', props.className])}
      />
    </div>
  );
};

const Thumb = (props: ImgProps) => {
  return (
    <div className="miq-img-thumb-wrapper">
      <Image
        {...props}
        src={props.thumb || props.src}
        thumb_sq={props.thumb || props.thumb_sq}
        src_mobile={props.thumb || props.src_mobile}
        className={getCN(['miq-img-thumb', props.className])}
      />
    </div>
  );
};

const Square = (props: ImgProps) => {
  return (
    <div className="miq-img-square-wrapper">
      <Image
        {...props}
        src={props.thumb_sq || props.src}
        thumb={props.thumb_sq || props.thumb}
        src_mobile={props.thumb_sq || props.src_mobile}
        className={getCN(['miq-img-square', props.className])}
      />
    </div>
  );
};

const Picture = (props: ImgProps) => {
  const src = getImgUrl(props.src);
  const src_mobile = getImgUrl(props.src_mobile);

  return (
    <picture>
      <source srcSet={src} media={`(min-width: 768px)`} />
      <Image {...props} src={src_mobile || src} className={getCN(['miq-img-picture', props.className])} />
    </picture>
  );
};

const Image = (props: ImgProps): JSX.Element => {
  const src = getImgUrl(props.src);
  const thumb = getImgUrl(props.thumb);
  const src_mobile = getImgUrl(props.src_mobile);

  const sizes = `(max-width: 640px) ${props.width_mobile}px, (max-width: 768px) ${props.width_thumb}px, ${props.width}px`;
  const srcSet = `${src_mobile} ${props.width_mobile}w, ${thumb} ${props.width_thumb}w, ${src} ${props.width}w`;
  const alt = props.alt_text || props.title || '';
  const ariaLabel = props.aria_label || props.title;

  let orientation;
  const { width, height } = props;
  if (width && height) orientation = width < height ? 'portrait' : width === height ? 'square' : 'landscape';

  return (
    <img
      srcSet={srcSet}
      sizes={sizes}
      src={src}
      alt={alt}
      title={props.title}
      aria-label={ariaLabel}
      className={getCN(['miq-img', orientation, props.className])}
    />
  );
};

//#endregion COMPONENTS

//#region Img

export default class Img extends AbstractModel {
  public static HorizontalGallery = HorizontalGallery;
  public static VerticalGallery = VerticalGallery;
  public static Mobile = Mobile;
  public static Thumb = Thumb;
  public static Square = Square;
  public static Picture = Picture;
  public static Image = Image;

  static get Fields() {
    return Object.freeze({
      src: 'src',
      src_mobile: 'src_mobile',
      thumb: 'thumb',
      thumb_sq: 'thumb_sq',
      alt_text: 'alt_text',
      caption: 'caption',
      width: 'width',
      heigth: 'heigth',
      size: 'size',
      width_thumb: 'width_thumb',
      heigth_thumb: 'heigth_thumb',
      size_thumb: 'size_thumb',
      width_thumb_sq: 'width_thumb_sq',
      heigth_thumb_sq: 'heigth_thumb_sq',
      size_thumb_sq: 'size_thumb_sq',
      width_mobile: 'width_mobile',
      heigth_mobile: 'heigth_mobile',
      size_mobile: 'size_mobile',
    });
  }
}

//#endregion Img

export class AbstractCrudStaffImgModel extends AbstractCrudStaffModel {
  public static HorizontalGallery = HorizontalGallery;
  public static VerticalGallery = VerticalGallery;
  public static Mobile = Mobile;
  public static Thumb = Thumb;
  public static Square = Square;
  public static Picture = Picture;
  public static Image = Image;
}
