import React, { useEffect, useState, createContext, useMemo } from 'react';
import { getSharedData } from '@miq/utiljs';

const initialState = { isLoaded: false, site: null, user: null, perms: null };

export type SharedDataState = typeof initialState;

export const SharedDataCtx = createContext<typeof initialState>(initialState);

export interface SharedDataProviderProps {
  children: React.ReactNode;
}

export const SharedDataProvider: React.FC<SharedDataProviderProps> = ({ children }) => {
  const [value, setValue] = useState<SharedDataState>(initialState);

  const path = window.location.href;

  useEffect(() => {
    getSharedData()
      .then((data) => setValue({ ...data, isLoaded: true }))
      .catch(() => setValue(initialState));
  }, [path]);

  const sharedCtx = useMemo(() => {
    const updateData = (newData: Object = {}) => setValue({ ...value, ...newData });
    const updateSite = (siteData: Object) => {
      setValue({ ...value, site: { ...value.site, ...siteData } });
    };

    const updateUser = (userData) => {
      setValue({ ...value, user: { ...value.user, ...userData } });
    };
    return { ...value, updateUser, updateSite, updateData };
  }, [value]);

  if (!value.isLoaded) return null;

  return <SharedDataCtx.Provider value={sharedCtx}>{children}</SharedDataCtx.Provider>;
};
